/*
  This boilerplate is based in ITCSS and SMACSS
*/
*,
*::before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "sans-serif";
}

@font-face {
  font-family: "bold";
  src: url("../fuentes/Kanit/Kanit-Bold.ttf") format("truetype");
}

@font-face {
  font-family: "light";
  src: url("../fuentes/Kanit/Kanit-Light.ttf") format("truetype");
}

@font-face {
  font-family: "mediun";
  src: url("../fuentes/Kanit/Kanit-Medium.ttf") format("truetype");
}

@font-face {
  font-family: "regular";
  src: url("../fonts/Kanit/Kanit-Regular.ttf") format("truetype");
}

@font-face {
  font-family: 'icomoon';
  src: url("../icon/icomoon.eot?wgwy6n");
  src: url("../icon/icomoon.eot?wgwy6n#iefix") format("embedded-opentype"), url("../icon/icomoon.ttf?wgwy6n") format("truetype"), url("../icon/icomoon.woff?wgwy6n") format("woff"), url("../icon/icomoon.svg?wgwy6n#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

