.button {
  font-size: 1.2em;
  transition: all .25s;
  font-family: "bold", sans-serif;
}

.button.primary {
  background: #c0aa83;
  color: #242424;
  line-height: 1.9em;
  padding: 10px 40px;
  border: 2px solid #c0aa83;
}

.button.primary:hover {
  border-color: #242424;
  background: transparent;
  color: #242424;
  transition: all .25s;
  text-decoration: none;
}

.button.secondary {
  background: #242424;
  color: #f4f4f4;
  border: 1px solid #f4f4f4;
  border-radius: 3px;
}

.button.secondary:hover {
  background: transparent;
  color: red;
  transition: all .25s;
  text-decoration: none;
}

.nosotros-main {
  margin: 0px 0px;
  padding: 30px 0;
}

@media screen and (min-width: 768px) {
  .nosotros-main {
    margin: 0px 0px;
    background: url(../img/about-index-img.png) no-repeat;
    background-size: 100%;
    background-position: center 70%;
  }
}

@media screen and (min-width: 1024px) {
  .nosotros-main {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 2em;
    background-size: 60%;
    background-position: center left;
    padding: 50px 0;
  }
}

.nosotros-main__info {
  text-align: left;
  padding: 20px 0px;
}

@media screen and (min-width: 768px) {
  .nosotros-main__info {
    background-size: 40%;
  }
}

@media screen and (min-width: 1024px) {
  .nosotros-main__info {
    text-align: left;
    background-size: 90%;
  }
}

.nosotros-main__info h3 {
  font-family: "bold", sans-serif;
  color: #c0aa83;
  font-weight: 700;
  font-size: 1.2em;
}

@media screen and (min-width: 768px) {
  .nosotros-main__info h3 {
    font-size: 2em;
  }
}

.nosotros-main__info h1 {
  font-family: "bold", sans-serif;
  color: #242424;
  font-weight: 700;
  font-size: 2em;
}

@media screen and (min-width: 768px) {
  .nosotros-main__info h1 {
    font-size: 3.5em;
  }
}

.nosotros-main__info p {
  width: 100%;
  font-family: "regular", sans-serif;
  font-size: 1em;
  font-weight: 400;
  line-height: 30px;
  color: #242424;
  margin: 40px 0;
}

@media screen and (min-width: 768px) {
  .nosotros-main__info p {
    width: 80%;
    font-size: 1.3em;
    line-height: 36px;
  }
}

.nosotros-main__info span {
  width: 100%;
  text-align: center;
  font-family: "bold", sans-serif;
  color: #242424;
  font-size: 1.5em;
}

.nosotros-main__service__items {
  margin: 20px 0;
  text-align: left;
}

@media screen and (min-width: 768px) {
  .nosotros-main__service__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 2em;
    text-align: left;
  }
}

.nosotros-main__service__items__item {
  margin-top: 50px;
}

@media screen and (min-width: 768px) {
  .nosotros-main__service__items__item {
    margin-top: 0px;
  }
}

.nosotros-main__service__items__item h2 {
  font-family: "bold", sans-serif;
  color: #242424;
  font-weight: 700;
  font-size: 1.1em;
  margin: 15px 0;
}

.nosotros-main__service__items__item p {
  font-family: "light", sans-serif;
  color: #242424;
  font-size: 1em;
  margin: 0px;
}

@media screen and (min-width: 768px) {
  .nosotros-main__service__items__item p {
    font-size: 0.9em;
  }
}

@media screen and (min-width: 768px) {
  .productos-main__blocktitle {
    height: 200px;
  }
}

.productos-main__blocktitle__title {
  text-align: center;
}

.productos-main__blocktitle__title span {
  font-family: "bold", sans-serif;
  color: #f4f4f4;
  opacity: 0.4;
  font-weight: 700;
  font-size: 6em;
  display: none;
}

@media screen and (min-width: 768px) {
  .productos-main__blocktitle__title span {
    position: absolute;
    left: 0;
    right: 0;
    display: block;
  }
}

@media screen and (min-width: 1024px) {
  .productos-main__blocktitle__title span {
    font-size: 7em;
  }
}

.productos-main__blocktitle__title h1 {
  font-family: "bold", sans-serif;
  color: #44200e;
  font-weight: 700;
  font-size: 3em;
}

@media screen and (min-width: 768px) {
  .productos-main__blocktitle__title h1 {
    position: relative;
    top: 40px;
  }
}

@media screen and (min-width: 1024px) {
  .productos-main__blocktitle__title h1 {
    font-size: 4em;
    top: 50px;
  }
}

@media screen and (min-width: 1024px) {
  .productos-main__blockproducts {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.productos-main__blockproducts__item {
  max-width: 100%;
}

.productos-main__blockproducts__item h2 {
  font-family: "bold", sans-serif;
  color: #c0aa83;
  font-weight: 700;
  font-size: 1.5em;
}

@media screen and (min-width: 1024px) {
  .productos-main__blockproducts__item {
    text-align: center;
  }
}

@media screen and (min-width: 768px) {
  .productos-main__blockproducts__item__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 40px 0;
  }
}

.productos-main__blockproducts__item__items div {
  text-align: center;
}

.productos-main__blockproducts__item__items div h3 {
  font-family: "bold", sans-serif;
  color: #44200e;
  font-weight: 500;
  font-size: 1.2em;
}

.productos-main__blockproducts__item__items div p {
  font-family: "light", sans-serif;
  color: "regular";
  font-weight: 600;
  font-size: 1em;
}

.productos-main__button {
  width: 100%;
  text-align: center;
}

.productos-main__button a {
  cursor: pointer;
}

.puntosventas-main {
  margin: 50px 0;
}

@media screen and (min-width: 1024px) {
  .puntosventas-main__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.puntosventas-main__items__item h2 {
  font-family: "bold", sans-serif;
  color: #242424;
  font-weight: 700;
  font-size: 2em;
}

@media screen and (min-width: 1024px) {
  .puntosventas-main__items__item h2 {
    font-size: 3.5em;
  }
}

.puntosventas-main__items__item p {
  font-family: "light", sans-serif;
  color: #242424;
  font-size: 1em;
  margin: 20px 0 40px 0px;
}

.puntosventas-main__items__item h3 {
  font-family: "bold", sans-serif;
  color: #c0aa83;
  font-size: 1.5em;
  margin: 20px 0 40px 0px;
}

@media screen and (min-width: 1024px) {
  .puntosventas-main__items__item h3 {
    font-size: 2em;
  }
}

@media screen and (min-width: 768px) {
  .puntosventas-main__items__item__paises {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.puntosventas-main__items__item__paises div {
  font-family: "bold", sans-serif;
  color: #242424;
  font-size: 1.2em;
  font-weight: bold;
  margin: 10px 0px;
}

.puntosventas-main__items__item__paises div li {
  list-style: none;
}

.puntosventas-main__items__item__paises div li i {
  font-weight: bold;
}

.clientes-main__distribuidor {
  padding: 50px 0 200px 0;
}

@media screen and (min-width: 768px) {
  .clientes-main__distribuidor {
    display: grid;
    grid-template-columns: 1fr 1fr;
    background: url(../img/coffee-cup.png) no-repeat;
    background-size: 50%;
    background-position: 10% 70%;
  }
}

@media screen and (min-width: 1024px) {
  .clientes-main__distribuidor {
    background-size: 40%;
  }
}

.clientes-main__distribuidor__slogan {
  font-family: "bold", sans-serif;
}

.clientes-main__distribuidor__slogan h3, .clientes-main__distribuidor__slogan h2 {
  font-size: 2em;
  color: #242424;
  margin: 0px;
  position: relative;
  top: 20px;
}

@media screen and (min-width: 1024px) {
  .clientes-main__distribuidor__slogan h3, .clientes-main__distribuidor__slogan h2 {
    font-size: 3em;
  }
}

.clientes-main__distribuidor__slogan h1 {
  font-size: 6.5em;
  color: #c0aa83;
  margin: 0px;
}

@media screen and (min-width: 1024px) {
  .clientes-main__distribuidor__slogan h1 {
    font-size: 8em;
  }
}

.clientes-main__distribuidor__slogan h2 {
  font-size: 2em;
  color: #242424;
  margin: 0px;
  position: relative;
  top: -20px;
}

@media screen and (min-width: 1024px) {
  .clientes-main__distribuidor__slogan h2 {
    width: 60%;
  }
}

.clientes-main__distribuidor__contactanos h3 {
  font-family: "bold", sans-serif;
  font-size: 1.5em;
  color: #242424;
  margin: 30px 0 20px 0;
}

@media screen and (min-width: 1024px) {
  .clientes-main__distribuidor__contactanos h3 {
    font-size: 3em;
  }
}

.clientes-main__distribuidor__contactanos p {
  font-family: "regular", sans-serif;
  font-size: 1em;
  margin: 10px 0 40px 0;
}

.clientes-main__numeros {
  background: #44200e;
  padding: 100px 0;
  color: #c0aa83;
}

@media screen and (min-width: 768px) {
  .clientes-main__numeros {
    background: url(../img/parrallax-cafe-count.jpg) no-repeat;
    background-attachment: fixed;
    background-size: cover;
    background-position: center center;
    overflow: hidden;
  }
}

.clientes-main__numeros__main {
  text-align: center;
}

@media screen and (min-width: 768px) {
  .clientes-main__numeros__main {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

.clientes-main__numeros__main div {
  margin: 50px 0;
  font-family: "bold", sans-serif;
  color: #c0aa83;
}

.clientes-main__numeros__main div h1 {
  font-weight: 700;
  font-size: 3em;
}

.clientes-main__numeros__main div h4 {
  font-family: "light", sans-serif;
  color: #f4f4f4;
  font-weight: 500;
  font-size: 1.2em;
}

.testimonios-main__content__socios {
  display: grid;
}

@media screen and (min-width: 768px) {
  .testimonios-main__content__socios {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (min-width: 1024px) {
  .testimonios-main__content__socios {
    grid-template-columns: repeat(6, 1fr);
    padding: 50px 0px;
  }
}

.testimonios-main__content__socios div {
  width: 250px;
  height: 250px;
  margin: 0px auto;
}

@media screen and (min-width: 768px) {
  .testimonios-main__content__socios div {
    width: auto;
    height: auto;
    margin: 0px 0px;
  }
}

.testimonios-main__content__testigos {
  overflow: hidden;
  display: grid;
}

@media screen and (min-width: 768px) {
  .testimonios-main__content__testigos {
    grid-template-columns: repeat(2, 1fr);
    padding: 0px 0 50px 0px;
  }
}

@media screen and (min-width: 768px) {
  .testimonios-main__content__testigos__datos {
    align-items: center;
    display: flex;
  }
}

.testimonios-main__content__testigos__datos div {
  text-align: center;
}

.testimonios-main__content__testigos__datos div h4 {
  font-family: "bold", sans-serif;
  color: #c0aa83;
  font-weight: 700;
  font-size: 1.5em;
}

@media screen and (min-width: 1024px) {
  .testimonios-main__content__testigos__datos div h4 {
    font-size: 2.5em;
  }
}

.testimonios-main__content__testigos__datos div h2 {
  font-family: "bold", sans-serif;
  color: #44200e;
  font-weight: 700;
  font-size: 2em;
}

@media screen and (min-width: 1024px) {
  .testimonios-main__content__testigos__datos div h2 {
    font-size: 3.5em;
  }
}

.testimonios-main__content__testigos__datos div span {
  font-family: "bold", sans-serif;
  color: #242424;
  font-size: 1em;
}

.testimonios-main__content__testigos__datos div p {
  color: #242424;
  font-family: "regular", sans-serif;
  font-size: 1em;
  margin: 20px 0px;
}

@media screen and (min-width: 1024px) {
  .testimonios-main__content__testigos__datos div p {
    text-align: center;
    font-size: 1.2em;
    width: 80%;
    margin: 40px auto;
  }
}

.testimonios-main__content__testigos__datos div i {
  font-size: 3em;
}

.testimonios-main__content__testigos__img {
  position: relative;
  z-index: 199;
}

.testimonios-main__content__testigos__img img {
  position: relative;
  left: 120px;
  z-index: 999999;
}

.l-container {
  width: 90%;
  max-width: 1300px;
  margin-left: auto;
  margin-right: auto;
}
